/*==========================
	Custom css
==========================*/

/* Body */

.container {
  max-width: 1270px;
}
.text strong {
  border-bottom: 1px dashed;
  font-weight: 700;
}
.btn.shadow {
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.25);
}
.btn.white:hover {
  color: #fff !important;
}

.main-slider .slide {
  /* height: 800px; */
  /* height: 700px; */
  background-position: center;
  position: relative;
  background-size: cover;
}
.main-slider .slide .content {
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  left: 0;
}
.main-slider .content span {
  font-size: 28px;
  line-height: 1.1;
  margin-bottom: 15px;
  display: block;
  color: #fff;
  font-family: "Amita", sans-serif;
  font-weight: 700;
}
.main-slider .title {
  font-size: 78px;
  color: #fff;
  line-height: 1;
  margin-bottom: 0;
  text-transform: capitalize;
  font-weight: 900;
  text-shadow: 0px 10px 40px rgba(0, 0, 0, 0.3);
}
.main-slider .sub-title {
  font-size: 28px;
  color: #fff;
  margin-top: 10px;
  margin-bottom: 50px;
  line-height: 1;
  font-weight: 300;
  font-family: "Poppins", sans-serif;
}
.main-slider a {
  margin: 0 5px 5px 0;
}
.main-slider .owl-next,
.main-slider .owl-prev {
  background: rgba(0, 0, 0, 0.3);
  color: #fff;
  padding: 30px 5px;
  font-size: 20px;
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
}
.main-slider .owl-next:hover,
.main-slider .owl-prev:hover {
  background: #fff;
  color: #000;
}

/* Banner */

.banner .bnr-bg {
  height: 100vh;
  background-position: center;
  position: relative;
  background-size: cover;
}
.banner .title {
  font-size: 89px;
  color: rgb(255, 255, 255);
  white-space: no-wrap;
  font-family: Barlow, sans-serif;
  text-transform: uppercase;
  font-weight: 800;
  line-height: 99px;
  border-width: 0;
  margin: 0;
  text-align: center;
  letter-spacing: 0px;
  padding-top: 70px;
}
.banner .info {
  font-size: 20px;
  font-family: Barlow, sans-serif;
  font-weight: 400;
  line-height: 30px;
  text-align: center;
  margin: 0;
  padding: 0;
  letter-spacing: 0;
  color: rgb(255, 255, 255);
  padding: 20px 0;
}
.banner .container {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 50%;
}
.banner .bnr-logo {
  display: inline-block;
  margin-top: 20px;
}

/* Header */
.header-style-2 .header-nav .nav > li > a {
  font-family: "Amita", sans-serif;
}
.header-style-2 .main-bar:after {
  content: "";
  background-image: url(../images/bg1.png);
  position: absolute;
  bottom: -8px;
  width: 100%;
  height: 8px;
  background-size: 80px;
  background-position: bottom;
  left: 0;
}
.header-style-1 .header-nav .nav > li > a span {
  display: block;
  font-weight: 700;
  color: #000;
  line-height: 16px;
  font-size: 12px;
  margin-top: 4px;
  opacity: 0.6;
}
.header-style-1 .header-nav .nav > li > a {
  border: 0 !important;
  padding: 40px 12px 20px;
  margin-right: 4px;
  border-radius: 0 0 6px 6px;
  transition: all 0.5s;
  font-family: Amita;
  background: #fff;
}
.header-style-1 .header-nav .nav > li.active > a,
.header-style-1 .header-nav .nav > li:hover > a {
  background: #5fcac7;
  color: #fff;
  transition: all 0.5s;
}
.header-style-1 .header-nav .nav > li.active > a span {
  color: #fff;
}
.header-style-1 .logo-header {
  padding: 20px 0 18px;
}
.header-style-1 .logo-header img {
  max-width: 100%;
}
.header-style-1.header-transparent .is-fixed .main-bar {
  background-color: transparent;
}
.header-social li {
  display: inline-block;
  margin-left: 0;
}
.header-social li a {
  font-size: 16px;
  color: #000;
  line-height: 30px;
  background: #fff;
  padding: 35px 10px 37px;
  border-radius: 0 0 6px 6px;
  display: block;
  width: 40px;
  text-align: center;
  transition: all 0.5s;
}
.header-social li a:hover {
  background-image: linear-gradient(0deg, #945d34, rgba(148, 93, 52, 0.2));
  transition: all 0.5s;
}
.header-style-1 .extra-nav {
  padding: 0;
}

/* dlab bnr inr */
.dlab-bnr-inr {
  text-align: left;
  background-size: cover;
  background-position: center top;
}
.dlab-bnr-inr .dlab-bnr-inr-entry {
  vertical-align: middle;
  display: table-cell;
  padding-top: 20px;
  text-align: center;
}
.dlab-bnr-inr .breadcrumb-row {
  background-color: rgba(255, 255, 255, 0.2);
  padding: 10px 20px 10px;
  border-radius: 6px;
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
}
.dlab-bnr-inr .breadcrumb-row ul li a {
  opacity: 1;
}
.dlab-bnr-inr .breadcrumb-row ul li {
  font-size: 13px;
  font-weight: 500;
  color: #fff !important;
}
.dlab-bnr-inr .breadcrumb-row ul li:after {
  content: "\f101";
  margin-left: 15px;
  font-weight: 500;
  margin-right: 5px;
}

/* Rev Slider */
.main-slider-one .tp-mask-wrap {
  overflow: unset !important;
}
.main-slider-one .tparrows {
  opacity: 0;
  transition: all 0.5s;
}
.main-slider-one:hover .tparrows {
  opacity: 1;
  transition: all 0.5s;
}
.rev-btn .btn.white:hover {
  background-color: #f7f7f7;
  color: #222 !important;
}

/* Section Head */
.section-head .icon-bx {
  margin-bottom: 15px;
}
.section-head.text-white p {
  color: #fff;
}
.section-head p {
  font-weight: 300;
  color: #222;
  font-size: 20px;
}
.section-head h2 {
  font-size: 42px;
  line-height: 62px;
  font-weight: 700;
  margin-bottom: 0;
}
.section-head h3 {
  font-size: 32px;
  font-weight: 700;
  line-height: 42px;
  margin-bottom: 10px;
}
.section-head h4 {
  font-size: 30px;
  line-height: 40px;
  margin-bottom: 5px;
}
.section-head .text-bold {
  font-weight: 500;
  color: #aaaaaa;
  font-size: 18px;
}
.section-head .main-text {
  font-size: 28px;
  line-height: 52px;
  max-width: 1000px !important;
  margin-top: 30px;
}
.section-head strong {
  font-weight: 700;
  border-bottom: 1px dashed;
}
.section-head {
  margin-bottom: 60px;
}
.dlab-bnr-inr:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-image: url(../images/bg1.png);
  background-size: 80px;
  height: 20px;
  background-position: top;
  background-repeat: repeat-x;
  transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  z-index: 1;
}
.bg-line-top:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-image: url(../images/bg1.png);
  height: 20px;
  background-position: top;
  background-repeat: repeat-x;
  background-size: 80px;
}
.bg-line-bottom:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-image: url(../images/bg1.png);
  background-size: 80px;
  height: 20px;
  background-position: top;
  background-repeat: repeat-x;
  transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
}
/* Separator Style 1 */
.dlab-separator.style1 {
  width: 60px;
  height: 4px;
  display: block;
}
.text-center .dlab-separator.style1 {
  margin-left: auto;
  margin-right: auto;
}

/* Service Box 1 */
.service-area1 {
  position: relative;
  top: -80px;
}
.service-box1 {
  margin-bottom: 20px;
  border-radius: 6px;
  box-shadow: 0px 10px 35px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
  height: 500px;
  display: flex;
  align-items: flex-end;
  transition: all 0.2s ease-in-out;
  background-size: cover;
  z-index: 1;
}
.service-box1:after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 50%;
  width: 100%;
  background-image: linear-gradient(0deg, #001817, rgba(0, 24, 23, 0));
}
.service-box1 .icon-content {
  padding: 40px 25px;
  z-index: 99;
  position: relative;
  width: 100%;
}
.service-box1 .dlab-tilte {
  font-size: 28px;
  line-height: 40px;
  font-weight: 700;
  margin-bottom: 0px;
}
.service-box1:hover {
  transform: scale(1.06) rotate(-3deg);
  transition: all 0.22s ease-in-out;
}
.service-box1 p {
  color: #fff;
  margin-bottom: 15px;
  font-size: 14px;
}
.service-box1 .dlab-separator {
  margin-bottom: 25px;
}
.service-area1 .section-head {
  margin: 50px 0 0;
}

/* Service Box 2 */
.service-box2 {
  display: flex;
  align-items: center;
  padding: 20px 0;
}
.service-box2 .icon-bx {
  margin-right: 30px;
}
.service-box2 .icon-cell img {
  width: 64px;
  max-width: 64px;
}
.service-box2 .dlab-tilte {
  font-size: 24px;
  line-height: 30px;
}
.service-box2 p {
  color: #fff;
}

/* About Thumb */
.about-thumb {
  background-image: url(../images/pattern.svg);
  padding: 0 0 40px 0px;
  margin: 45px 40px 0 0;
}
.about-thumb img {
  border-radius: 5px;
  margin: -45px 0 0 40px;
  width: 100%;
}

/* faq-style1 */
.acod-head a:after {
  font-family: "fontawesome";
  content: "\f106";
  font-size: 24px;
}
.acod-head a.collapsed:after {
  font-family: "fontawesome";
  content: "\f107";
  font-size: 24px;
}
.faq-style1 .panel {
  margin-bottom: 10px;
  box-shadow: 0 10px 40px 0px rgba(0, 0, 0, 0.15);
}
.faq-style1 .acod-head a.collapsed:hover,
.faq-style1 .acod-head:hover a {
  color: #fff;
}
.faq-style1 .acod-head a {
  color: #ffffff;
  background-color: #5fcac7;
  border: 0;
  border-radius: 6px;
  font-size: 20px;
  font-family: "Open Sans", sans-serif;
  padding: 20px 40px 20px 25px;
}
.faq-style1 .acod-head a:after {
  color: #fff;
}
.faq-style1 .acod-head a.collapsed {
  color: #fff;
  background-color: #5fcac7;
}
.faq-style1 .acod-head a i {
  margin-right: 10px;
}
.faq-style1 .acod-body {
  border: 0;
  margin: 0;
}
.faq-style1 .acod-body .acod-content {
  margin: 0;
  background: #fff;
  padding: 20px;
}
.faq-style1 .acod-body .acod-content :last-child {
  margin-bottom: 0;
}
.faq-style1 .list-check li:before {
  content: "\f00c";
  font-family: fontawesome;
}
.faq-style1 .list-check {
  display: inline-flex;
  flex-wrap: wrap;
}
.faq-style1 .list-check li {
  width: 33.33%;
  font-size: 14px;
  padding-top: 8px;
  padding-bottom: 8px;
}
.faq-style1 .list-check li a {
  color: #000;
}
.faq-style1 .alignleft {
  margin: 5px 25px 10px 0;
}

/* faq-style1 */
.faq-style2 .panel {
  margin-bottom: 10px;
}
.faq-style2 .acod-head a {
  background-color: #ffffff;
  font-size: 18px;
  border-radius: 10px 10px 0 0;
  font-family: "Open Sans", sans-serif;
  padding: 20px 40px 20px 25px;
  border: 1px solid rgba(167, 167, 167, 0.2);
  font-weight: 700;
}
.faq-style2 .acod-head a.collapsed {
  border-radius: 10px;
  font-weight: 600;
}
.faq-style2 .acod-body {
  border-radius: 0 0 10px 10px;
  background-color: #ffffff;
}

/* Title bx */
.title-bx {
  margin-bottom: 30px;
}
.title-bx h4 {
  font-weight: 600;
  display: inline-block;
  margin-bottom: 0;
  margin-left: 10px;
  font-size: 20px;
}
.title-bx .icon-bx-xs {
  background-color: #5fcac7;
  color: #fff;
  display: inline-block;
}

/* Port Box 1 */
.port-box1 {
  position: relative;
  overflow: hidden;
  border-radius: 12px;
  box-shadow: 0px 20px 50px 0 rgba(0, 0, 0, 0.15);
  z-index: 2;
}
.port-box1 .dlab-info {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  transition: all 0.5s;
}

.port-box1 .dlab-media {
  background: #5fcac7;
}
.port-box1 .dlab-media img {
  transition: all 0.5s ease 0s;
  transform: scale(1.2);
  -moz-transform: scale(1.2);
  -webkit-transform: scale(1.2);
  -ms-transform: scale(1.2);
  -o-transform: scale(1.2);
}
.port-box1 .dlab-info .title {
  margin-bottom: 0;
  padding: 50px 30px 30px;
  background-image: linear-gradient(0deg, #005b5f, rgba(0, 24, 23, 0));
  font-size: 24px;
  line-height: 1.3;
  text-transform: capitalize;
}
.port-box1 .dlab-info .hover {
  padding: 30px;
  background-color: #5fcac7;
  transform: translateY(100%);
  width: 100%;
  transition: all 0.5s;
}
.port-box1:hover .dlab-info .hover {
  transform: translateY(0);
}
.port-box1 .dlab-info .hover p {
  margin-bottom: 0;
}
.port-box1:hover .hover {
  margin-top: 0;
  transition: all 0.5s;
}
.port-box1:hover .dlab-info {
  bottom: 0;
}
.port-box1:hover .dlab-media img {
  transform: translateY(0);
  -moz-transform: translateY(0);
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  -o-transform: translateY(0);
  transition: all 0.5s ease 0s;
}
/* Counter Style 1 */
.counter-style-1 .counter {
  display: inline-block;
  font-weight: 600;
  font-size: 90px;
  line-height: 90px;
}
.counter-style-1 .counter-num {
  display: block;
}
.counter-style-1 .counter-num small {
  font-size: 50px;
  font-weight: 700;
}

/* About Us */
.about-area1 {
  background-color: #5fcac7;
  box-shadow: 0px 20px 50px 0 rgba(0, 0, 0, 0.1);
}
.about-area1 .about-bx {
  padding: 45px 80px;
}
.about-area1 .about-bx .section-head p {
  font-size: 18px;
  padding-top: 0;
}
.about-area1 .about-bx .section-head {
  margin-bottom: 0;
}
.about-area1 .about-bx .section-head .icon-bx {
  margin: 40px 0 30px;
}
.about-area1 .about-bx p {
  color: #fff;
}

/* Blog */
.blog-post.blog-grid .dlab-info {
  background-color: #fff;
}
.blog-post.blog-grid .post-title {
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 10px;
}
.blog-post.blog-rounded {
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0px 10px 40px 0 rgba(0, 0, 0, 0.1);
}

/* Blog Lg */
.blog-post .dlab-info {
  padding: 30px;
}
.blog-post .dlab-post-text p {
  font-size: 16px;
}
.blog-post .post-author img {
  border-radius: 50%;
  margin-right: 10px;
}
.blog-post .post-author a {
  font-weight: 700;
}
.blog-post .dlab-post-meta ul li {
  font-size: 14px;
  display: block;
}
.blog-post .dlab-post-meta {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  clear: both;
}
.blog-post .dlab-post-meta li.post-date {
  color: #777;
}
.blog-post .dlab-post-meta li:after {
  content: none;
}
.blog-post .dlab-post-meta .post-author-thumb {
  float: left;
  margin-right: 15px;
  border-radius: 50%;
  overflow: hidden;
  width: 45px;
}

/* Side Bar */
.side-bar .widget {
  background-color: rgba(95, 202, 199, 0.1);
  padding: 30px;
  border-radius: 6px;
}
.side-bar .widget .widget-title.style-2 {
  text-transform: capitalize;
  font-size: 20px;
  line-height: 30px;
  border-bottom: 1px solid rgba(167, 167, 167, 0.2);
  margin-bottom: 20px;
}
.side-bar .widget .widget-title.style-2:after {
  content: none;
}
.widget.widget_tag_cloud .tagcloud a,
.dlab-post-tags .post-tags a {
  background-color: var(--color-primary);
  border-radius: 4px;
  border: 0;
  color: #fff;
  font-weight: 400;
  padding: 6px 12px;
  margin: 0 4px 8px 0;
  text-transform: capitalize;
  font-size: 12px;
}

/* search bx */
.search-bx.style-2 .form-control {
  padding: 10px 20px;
  border-radius: 6px;
  border: 0;
  margin-right: 10px;
  height: 50px;
  font-size: 16px;
}
.search-bx.style-2 button {
  padding: 10px 20px;
  border-radius: 6px;
  height: 50px;
}

/* btn 1 */
.btn.btn1 {
  text-transform: capitalize;
  padding: 4px 16px 4px 4px;
  border-radius: 4px;
}
.btn.btn1 i {
  background-color: rgba(0, 0, 0, 0.26);
  margin-right: 10px;
  border-radius: 4px;
  height: 24px;
  width: 24px;
  line-height: 24px;
}

/* Client Logo */
.client-logo:before {
  content: attr(data-name);
  line-height: 24px;
  font-weight: 400;
  color: #fff;
  background-color: #5fcac7;
  border-radius: 4px;
  font-size: 14px;
  padding: 2px 15px;
  opacity: 0;
  transition: all 0.5s;
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  z-index: 99;
}
.client-logo:hover:before {
  opacity: 1;
  transition: all 0.5s;
  top: -40px;
}
.client-area1 .client-logo {
  width: 100%;
  display: block;
  padding: 5px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  background-color: #fff;
}
.client-area1 .client-logo img {
  transform: rotateY(-360deg);
  transition: all 0.5s;
  transform-style: preserve-3d;
  width: 100%;
}
.client-area1 .client-logo:hover img {
  transform: rotateY(0deg);
  transition: all 0.5s;
}

/* Footer */
.site-footer .footer-top {
  background-color: transparent;
  background-image: linear-gradient(
    90deg,
    rgba(0, 0, 0, 0.5),
    rgba(0, 0, 0, 0.5)
  );
}
.site-footer .widget .footer-title {
  font-size: 20px;
  font-weight: 700;
  color: #fff;
  border-bottom: 1px solid rgba(167, 167, 167, 0.5);
  padding-bottom: 20px;
  margin-bottom: 20px;
  line-height: 20px;
}
.site-footer .widget_services li:before {
  color: #fff;
}
.site-footer .widget_services li a {
  color: #fff;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}
.site-footer .widget p {
  font-size: 16px;
  color: #fff;
  line-height: 30px;
}
.site-footer .footer-bottom {
  background-color: #5fcac7;
  border-top: 0;
}
.footer-bottom a:hover {
  color: #fff;
  opacity: 0.8;
}

/* work hour list */
.work-hour-list {
  margin: 30px 0 0;
}
.work-hour-list li {
  font-size: 16px;
  color: #fff;
  padding: 15px 0;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.work-hour-list li:last-child {
  padding-bottom: 0;
}
.work-hour-list li .timing {
  margin: 0 0 0 10px;
  white-space: nowrap;
}
.work-hour-list li span {
  line-height: 12px;
}
.work-hour-list li .day {
  position: relative;
  margin: 0;
  width: 100%;
  display: flex;
  align-items: flex-end;
}
.work-hour-list li .day:after {
  content: "";
  height: 1px;
  width: 100%;
  display: block;
  border-bottom: 1px dashed rgba(255, 255, 255, 0.4);
}
.work-hour-list li .day span {
  webkit-box-flex: 0;
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
  margin-right: 10px;
  position: relative;
}

/* Widget Getintuch */
.widget_getintuch li {
  padding-left: 45px;
  margin-bottom: 5px;
}
.widget_getintuch ul li i {
  background-color: #fff;
