.menu-btn span {
  position: absolute;
  width: 6px;
  height: 6px;
  background: #fff;
  margin-left: -3px;
  border-radius: 4px;
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
}
.menu-btn span:first-child {
  left: 50%;
}
.menu-btn span:nth-child(2n) {
  top: 50%;
  left: 50%;
  margin-top: -3px;
}
.menu-btn span:last-child {
  bottom: 0;
  left: 50%;
}
.menu-btn span:before,
.menu-btn span:after {
  content: "";
  width: inherit;
  height: inherit;
  background: inherit;
  position: inherit;
  border-radius: inherit;
  transition: inherit;
  -moz-transition: inherit;
  -ms-transition: inherit;
  -webkit-transition: inherit;
  -o-transition: inherit;
}
.menu-btn span:before {
  left: -12px;
}
.menu-btn span:after {
  right: -12px;
}
